<template>
	<section>
		<!-- checkedItemList: {{ checkedItemList }}
		<br />
		selectedFilters: {{ selectedFilters }} -->
		<!-- {{selectedFilters}} -->
		<Filters
			:dataSource="dataSource"
			:dataset="ds"
			:filterEl="filterEl"
			:filterSetting="filterSetting"
			:collection="collection"
			:referenceKey="referenceKey"
			:localeEl="localeEl"
			:isCheckbox="isCheckbox"
			:isAction="isAction"
			@refereshclicked="refreshData"
			:setThisReference="getThisReference"
			:staticFilter="staticFilter"
			@download="downloadData()"
			@filterCollection="filterCollection"
			:additionalInfo="additionalInfo"
			:cardViewConfig="cardViewConfig"
		/>

		<TableView
			:fetchData="fetchData"
			:perPage="limit"
			:fields="fields"
			:collection="collection"
			:referenceKey="referenceKey"
			:localeEl="localeEl"
			:isCheckbox="isCheckbox"
			:isAction="isAction"
			v-if="!isCardView"
			ref="gridview"
			@update:data:source="updateDatasource"
			@clear:selected:filter="clearSelectedFilter"
		/>

		<section v-else>
			<CardSkeleton :dataSource="limit" v-if="screen_loading" />
			<transition name="fade">
				<list-view
					v-show="!screen_loading"
					:fetchData="fetchData"
					:perPage="limit"
					:pageTitle="localeEl.PageTitle"
					ref="listview"
					@update:data:source="updateDatasource"
					@clear:selected:filter="clearSelectedFilter"
				>
					<template slot-scope="{ item, index }">
						<CardView
							:key="index"
							:index="index"
							:item="item"
							:fields="fields"
							:collection="collection"
							:referenceKey="referenceKey"
							:localeEl="localeEl"
							:isCheckbox="isCheckbox"
							:isAction="isAction"
						/>
					</template>
				</list-view>
			</transition>
		</section>

		<div v-if="showModal">
			<router-view></router-view>
		</div>
	</section>
</template>

<script>
import { mapState } from "vuex";
import ListView from "@/components/DataVisualisation/ListView/ListView.vue";
import TableView from "@/components/DataVisualisation/TableView/TableView.vue";

export default {
	components: {
		ListView,
		TableView,
	},

	data() {
		const dataSource = Object.freeze([]);
		return {
			screen_loading: true,
			dataSource: dataSource,
			ds: {},
			showModal: false,
			isLoading: false,
			limit: 50,
			additionalInfo:{},
			cardViewConfig: true
		};
	},
	created() {
		document.addEventListener("keydown", this.SwitchLayout);
		if (localStorage.getItem(`filters_${this.collection}`) === null) {
			this.$store.commit("OnSelectedFilters", this.defaultFilter);
		} else {
			this.$store.commit("OnSelectedFilters", this.localStorageFilter);
		}

		if (!this.$route.query.view && this.checkedItemList.length) {
			this.$store.commit("OnCheckedItem", []);
		}
	},
	beforeDestroy() {
		//Destroying addEventListener
		document.removeEventListener("keydown", this.SwitchLayout);
	},
	computed: {
		isCardView() {
			this.cardViewConfig =  this.cardView == false ? this.cardView : this.$config.CARD_VIEW
			if (this.cardViewConfig == true) {
				if (this.$route.query.view == "grid") {
					return false;
				}
				if (!this.$route.query.view || this.$route.query.view == "card") {
					return true;
				}
			} else {
				if (this.$route.query.view == "grid") {
					return true;
				} else if (!this.$route.query.view || this.$route.query.view == "card") {
					return false;
				}
			}
		},
		...mapState(["isActionPerformed", "checkedItemList", "selectedFilters"]),
		localeEl() {
			return this.$t(this.localeName);
		},
		localStorageFilter() {
			// const ls = localStorage.getItem(`filters_${this.collection}`)
			// if(this.defaultFilter.length && !localStorage.getItem(`filters_${this.collection}`)){
			// 	localStorage.setItem(`filters_${this.collection}`, JSON.stringify(this.defaultFilter))
			// }
			// // if(ls && this.defaultFilter.length){
			// // 	localStorage.setItem(`filters_${this.collection}`, JSON.stringify(ls));
			// // 	return JSON.parse(ls) || []
			// // }
			// // else{
			// // 	localStorage.setItem(`filters_${this.collection}`, JSON.stringify(this.defaultFilter));
			// // 	return this.defaultFilter
			// // }
			if (localStorage.getItem(`filters_${this.collection}`) === null) {
				return null;
			} else
				return JSON.parse(
					localStorage.getItem(`filters_${this.collection}`) || "[]"
				);
		},
	},
	watch: {
		$route: {
			immediate: true,
			handler: function (newVal) {
				this.showModal = newVal.meta && newVal.meta.showModal;
			},
		},
		isActionPerformed(newVal) {
			if (newVal == true) {
				this.dataSource = [];
				this.dataSource.length = 0;
				this.$store.commit("OnActionPerformed", false);
				this.fetchNewData();
			}
		},
		selectedFilters(newVal) {
			if (newVal) {
				if (!this.fetchData.isLoading) {
					this.fetchNewData();
				}
			}
		},
	},
	methods: {
		clearSelectedFilter() {
			this.$store.commit("OnCheckedItem", []);
		},

		fetchNewData() {
			this.dataSource = [];
			this.dataSource.length = 0;
			if (!this.isCardView) {
				this.$refs.gridview && this.$refs.gridview.refereshData();
			} else {
				this.$refs.listview && this.$refs.listview.refereshData();
			}
		},
		SwitchLayout(e) {
			// console.log(e)
			if (e.ctrlKey && e.shiftKey && e.which == 88) {
				if (!this.$route.query.view || this.$route.query.view == "grid")
					this.$router
						.push({ path: this.$route.path, query: { view: "card" } })
						.catch(() => {});
				else
					this.$router
						.push({ path: this.$route.path, query: { view: "grid" } })
						.catch(() => {});

				if (!this.$route.query.view || this.$route.query.view == "card")
					this.$router
						.push({ path: this.$route.path, query: { view: "grid" } })
						.catch(() => {});
				else
					this.$router
						.push({ path: this.$route.path, query: { view: "card" } })
						.catch(() => {});
			}
		},
		downloadData(options) {
			console.log("downloadData options ", options);
			options = options || {
				body: {
					filter: [...(this.staticFilter || []), ...(this.selectedFilters || [])],
					redirect: "follow",
				},
				headers: {
					Accept: "text/csv",
				},
			};
			const vm = this;
			vm.showNotificationSpinner("Downloading. Please Wait ...");
			this.$credCAPI
				.collection(`etf/${this.collection}/read`)
				.read(options)
				.then((result) => {
					if (result.status === "unsuccess") return;
					var blob = new Blob([result]);
					var url = window.URL.createObjectURL(blob);
					var a = document.createElement("a");
					a.href = url;
					a.download = "data.csv";
					vm.closeNotificationSpinner();
					a.click();
				})
				.catch((error) => {
					console.error(error);
					vm.closeNotificationSpinner();
				});
		},
		refreshData() {
			console.log("refresh clicked");
			this.fetchNewData();
		},
		updateDatasource(record) {
			this.$set(this, "dataSource", record);
		},
		fetchData({ start, limit }) {
			const _this = this;

			const vObj = {
				body: {
					filter: [...(this.staticFilter || []), ...this.selectedFilters],
					page: { start: "" + start, limit },
				},
			};

			if (this.fetchData.isLoading)
				return new Promise(function (resolve) {
					resolve(this.dataSource || []);
				});

			this.screen_loading = true;
			this.fetchData.isLoading = true;

			function transformNullIntoString(obj) {
				const keys = Object.keys(obj);
				keys.map((key) => {
					if (obj[key] == null) obj[key] = "";
				});
				return obj;
			}

			return this.$credCAPI
				.collection(`etf/${this.collection}/read`)
				.read(vObj)
				.then((response) => {
					if (response && response.length) {
						this.dataSource = Object.freeze(response.map(transformNullIntoString));
						const response_keys = Object.keys(response[0]);
						// this.fields = this.$_getFieldData(response_keys, this.localeEl.fields);
						this.fields = this.columns
							? this.$_getColumnData(this.columns, this.localeEl.fields)
							: this.$_getFieldData(response_keys, this.localeEl.fields);
					}
					this.screen_loading = false;
					this.fetchData.isLoading = false;
					return new Promise(function (resolve) {
						resolve(_this.dataSource);
					});
				})
				.catch((error) => {
					_this.screen_loading = false;
					_this.fetchData.isLoading = false;
					console.error("Error Fetch Data Table view ...", error);
					return new Promise(function (resolve) {
						resolve([]);
					});
				});
		},
		filterCollection(val){
			this.collectionArr = val
			this.refreshData()
		},
	},
};
</script>
