<template>
    <div class="card-header d-flex justify-content-between border-0">
        <span>{{ paginationLabel }}</span>
        <b-pagination
            v-on:change="onPagiChange"
            ref="pagination"
            size="sm"
            pills
            align="right"
            v-model="currentPage"
            :per-page="perPage"
            :total-rows="recordDetails.totalRecord"
            class="mb-0"
        ></b-pagination>
    </div>
</template>

<script>


export default {
    name : 'view-pagination',
    props: {
        value: {
            type: Number,
            default: 1
        },
        perPage: {
            type: Number,
            default: 10
        },
        callRestApi: {
            type: Function,
            required: true
        }
    },

    data() {
        return {
            localData: [],
            isBusy: false,
            firstInitialData: []
        }
    },

    computed: {

        currentPage: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit("input", value)
            }
        },

        paginationLabel(){
            let label = `Records ${this.recordDetails.fisrtRecord} - ${this.recordDetails.lastRecord}`
            if((this.recordDetails.totalRecord == null || this.recordDetails.totalRecord == "")) return "";
            return label += ` of ${this.recordDetails.totalRecord}`;
        },

        recordDetails() {
			let lastRecord = (this.value - 1) * this.perPage + this.perPage;
			let rc_interval = ((this.localData || [])[0] || {}).RC_INTERNAL;
			lastRecord = lastRecord > rc_interval ? rc_interval : lastRecord;
			return {
				fisrtRecord: (this.value - 1) * this.perPage + 1,
				lastRecord,
				totalRecord: rc_interval,
			};
		},
    },

    mounted() {
        this.onPageChanged(1);
    },

    methods: {

        onPagiChange(page) {
            this.$emit("clear:selected:filter");
            this.onPageChanged(page);
        },

        refereshData(){
            this.localData = [];
            this.firstInitialData = [];
            this.currentPage = 1;
            this.onPageChanged(1);
        },

        emitLocalData() {
            this.$emit("local:data:changed", this.localData);
        },
        
        handleResponse(response) {
            this.isBusy = false;
            this.$set(this, 'localData', response||[]);
            this.emitLocalData();
            this.$emit("busy:state", this.isBusy);
        },
        onPageChanged(page) {
            const vm = this;
            let start = ((parseInt(page) - 1) * parseInt(vm.perPage) + 1) - 1;
            vm.isBusy = true;
            vm.$emit("busy:state", vm.isBusy);
            vm.callRestApi({
                start,
                limit: vm.perPage
            })
            .then(response => {
                vm.handleResponse(response);
            })
            .catch(error => {
                vm.handleResponse([]);
                console.error(error);
            });
        }
    }
}
</script>