<template>
	<b-card
		:class="{
			selected: $action_checkCardIndex(item),
			deleted: item.DELETED == 'Y',
		}"
		no-body
	>
		<b-card-header style="border-bottom: solid 1px #eee; align-items: center">
			<h4 class="text-capitalize mb-0">
				<label
					><input
						:value="item"
						v-model="checkedItem_vm"
						type="checkbox"
						v-if="isCheckbox == true"
					/>
					{{ `${item.SALUTATION} ${item.AP_NAME}` || "-" }}</label
				>
			</h4>

			<div class="text-right">
				<b-badge pill :variant="item.AUTHORIZATION_STATUS | status_label_variant">{{
					item.AUTHORIZATION_STATUS | status_label($t("status_labels"))
				}}</b-badge>
				<component
					:is="Actions"
					:item="item"
					:routePath="$route.path"
					:collection="collection"
					:referenceKey="referenceKey"
					:localeEl="localeEl"
					:showIcons="false"
					:showDots="true"
					v-if="isAction == true"
				>
				</component>
			</div>
		</b-card-header>
		<b-card-body>
			<div class="card-content grid--3">
				<div class="content">
					<div class="hd">{{ localeEl.fields.AP_ID || "-" }}</div>
					<div class="cont text-truncate" :title="item.AP_ID">
						#{{ item.AP_ID || "-" }}
					</div>
				</div>
				<div class="content">
					<div class="hd">{{ localeEl.fields.CREATED_ON || "-" }}</div>
					<div class="cont text-truncate" :title="item.CREATED_ON">
						{{ item.CREATED_ON || "-" }}
					</div>
				</div>
				<div class="content">
					<div class="hd">{{ localeEl.fields.CATEGORY_CODE || "-" }}</div>
					<div class="cont text-truncate" :title="item.CATEGORY_CODE">
						{{ item.CATEGORY_CODE || "-" }}
					</div>
				</div>
			</div>
		</b-card-body>

