import { mapState } from "vuex";
export const ActionMixin = {
    data() {
        return {
            checkedArr: []
        };
    },
    computed: {
        ...mapState(["checkedItemList", "selectedFilters"]),
        Actions() {
            if (this.isAction == true && this.$route.meta.showModal != true)
                return () => import(`@/modules${this.$route.path}/components/Actions`);
        },
        checkedItem_vm: {
            get: function () {
                return this.checkedItemList || []
            },
            set: function (newValue) {
                this.$store.commit("OnCheckedItem", newValue || []);
            }
        },
        /*checkedArr() {
            return this.$_getCheckedItems({ items: this.checkedItemList, referenceKey: this.referenceKey })
        }*/
    },
    watch: {

    },
    filters: {
        

    },
    created() {

    },
    methods: {
        test(){
            console.log('sjflsjflkskfj')
        },
        onCheckedCard(referenceKey) {
            this.checkedArr = this.$_getCheckedItems({ items: this.checkedItemList, referenceKey: referenceKey })
            return this.checkedArr
        },
        onAction({ scope, action, url, data_ref, collection, confirm, referenceKey }, isProgress = false) {
            if (data_ref && Object.keys(data_ref).length) {
                this.$store.commit("OnCheckedItem", [data_ref]);
            }

            console.log(referenceKey)

            let _scope = scope ? scope : {}
            !isProgress && this.$store.commit("loading", true);
            const _this = this;
            if (confirm == false) {
                this.requestCall({ scope, action, url, data_ref, collection, confirm, referenceKey, _this, _scope, isProgress })
            }
            else {
                this.$_confirmMessage({ action: action })
                    .then(async (value) => {
                        if (value) {
                            this.requestCall({ scope, action, url, data_ref, collection, confirm, referenceKey, _this, _scope, isProgress })
                        }
                        else {
                            !isProgress && _this.$store.commit("loading", false);
                        }
                    })
                    .catch((err) => {
                        console.log(err)
                    });
            }
        },
        async requestCall({ scope, action, url, data_ref, collection, confirm, referenceKey, _this, _scope, isProgress }) {
            let processid = null;
            const vObj = {
                // filter: {
                ..._scope,
                // "data": _this.checkedArr,
                "data": _this.onCheckedCard(referenceKey || _this.referenceKey),
                // }
            };

            if (isProgress) {
                let PROCESS_ID = await _this.$showProcessStatus.getPorcessId();
                processid = collection + "_" + PROCESS_ID;
                vObj.filter.processid = processid
            }

            (function fn() {
                vObj["_ignore_prc_con"] = this.ignore_prc_con;
                this.$credCAPI
                    .collection(url)
                    .read({ body: vObj })
                    .then((response) => {
                        !isProgress && this.$responseHandler(response, fn, this, "noRouteChange");
                        !isProgress && this.$store.commit("loading", false);

                        if (isProgress) {
                            this.$store.commit("OnCheckedItem", undefined);
                            this.$store.commit("OnActionPerformed", true);
                        }
                    })
                    .catch((error) => console.error(error));
            }.call(_this));

            isProgress && _this.$showProcessStatus({ processName: processid })
        },
        $action_onDelete({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/delete`,
                action: (this.localeEl.actions['delete'] || 'delete').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onUnDelete({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/undelete`,
                action: (this.localeEl.actions['undelete'] || 'un delete').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onAuthorise({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/authorise`,
                action: (this.localeEl.actions['authorise'] || 'authorise').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onBankCreditApproved({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/creditapproved`,
                action: (this.localeEl.actions['bank_credit_approved'] || 'bank_credit_approved').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onConfirm({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/confirm`,
                action: (this.localeEl.actions['confirm'] || 'confirm').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onUnAuthorise({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/unauthorise`,
                action: (this.localeEl.actions['unauthorise'] || 'un authorise').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey
            });
        },
        $action_onSettle({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/settle`,
                action: (this.localeEl.actions['settle'] || 'settle').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey
            });
        },
        $action_onProcess({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/process`,
                action: (this.localeEl.actions['post'] || 'post').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey
            }, false);
        },
        $action_onCancel({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/cancel`,
                action: this.localeEl.actions['cancel'].toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey
            });
        },
        $action_Acknowledge({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/update/ack`,
                action: this.localeEl.actions['acknowledge'].toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
                confirm: false
            });
        },
        $action_ConfirmAll({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/create/all`,
                action: this.localeEl.actions['confirm_all'].toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $action_onUpdateSettlement({ collection, data_ref, scope, referenceKey }) {
            if (data_ref && Object.keys(data_ref).length) {
                this.$store.commit("OnCheckedItem", [data_ref]);
            }
            const arr = []
            this.checkedItemList.forEach(element => {
                arr.push(element[referenceKey || this.referenceKey])
            });
            this.$router.push({ path: `${this.$route.path}/Update_Settlement_Details`, query: { collection: collection, scope: scope, reference: arr.toString() } })
        },
        $action_onModify({ collection, data_ref, scope, referenceKey }) {
            if (data_ref && Object.keys(data_ref).length) {
                this.$store.commit("OnCheckedItem", [data_ref]);
            }
            this.$router.push({ path: collection, query: { reference: JSON.stringify(this.onCheckedCard(referenceKey || this.referenceKey)[0]) } });
        },
        $action_checkCardIndex(obj) {
            const index = this.checkedItemList.indexOf(obj);
            return (index > -1) ? true : false
        },
        $action_onRebalance({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/create`,
                action: (this.localeEl.actions['rebalance']).toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        downloadData(options) {
            options = options || {
                body: {
                    filter: [...(this.staticFilter || []), ...(this.selectedFilters || [])],
                    redirect: 'follow',
                },
                headers: {
                    Accept: "text/csv",
                }
            };
            this.$credCAPI
                .collection(`etf/${this.collection}/read`)
                .read(options)
                .then((result) => {
                    if (result.status === "unsuccess") return;
                    var blob = new Blob([result]);
                    var url = window.URL.createObjectURL(blob);
                    var a = document.createElement("a");
                    a.href = url;
                    a.download = 'data.csv';
                    a.click();
                })
                .catch((error) => console.error(error));
        },
        $action_onSendSTP({ collection, data_ref, scope, referenceKey }) {
            this.onAction({
                url: `etf/${collection}/stp_match/read`,
                action: (this.localeEl.actions['send_stp'] || 'send_stp').toLowerCase(),
                data_ref: data_ref || {},
                scope: scope,
                collection,
                referenceKey,
            });
        },
        $_customModal({ size, html, title }) {
            const h = this.$createElement;
            const messageVNode = h('div', { class: ['confirm-box'], domProps: { innerHTML: html || '' } });
            return this.$bvModal.msgBoxConfirm([messageVNode], {
                titleHtml: title || '',
                size: size,
                buttonSize: 'md',
                noCloseOnBackdrop: true,
                noCloseOnEsc: true,
                okVariant: 'primary',
                okTitle: 'Save',
                cancelTitle: 'Cancel',
                footerClass: 'p-2',
                hideHeaderClose: true,
                centered: true
            });
        },
    },
};
