											</th>
											<th style="border-top-right-radius: 20px; border-bottom-right-radius: 20px">
												<div>{{ localeEl.fields.CUSTOMER_RATE }}</div>
											</th>
										</tr>
									</thead>
									<ValidationObserver ref="formObserver" tag="tbody">
										<b-tr v-for="(item, index) in currencyList" :key="index">
											<b-td class="m-2 pt-3 pb-3">
												<FormElementWithValidation
