														ds: currencyOptions,
														'ds-code': 'code',
														'ds-name': 'label',
														name: `CURRENCY`,
														label: localeEl.fields['CURRENCY'],
														rules: { required: false, max: [255] },
														value: item.CURRENCY,
														static:true
													}"
													:locale="localeEl.fields"
													:v-model="'demo'"
												>
												</FormElementWithValidation>
											</b-td>
											<b-td class="m-2 pt-3 pb-3">
												<FormElementWithValidation
													:item="{
														type: 'number',
														name: `FC_AMOUNT`,
														label: localeEl.fields['FC_AMOUNT'],
														rules: { required: false, max: [255] },
														handlers: {
															keypress: $_handleDecimalInput,
														},
													}"
													:locale="localeEl.fields"
													:v-model="''"
												>
												</FormElementWithValidation>
											</b-td>
											<b-td class="m-2 pt-3 pb-3">
												<FormElementWithValidation
													:item="{
														type: 'number',
														name: `CUSTOMER_RATE`,
														label: localeEl.fields['CUSTOMER_RATE'],
														rules: { required: false, max: [255] },
														handlers: {
															keypress: $_handleDecimalInput,
														},
													}"
													:locale="localeEl.fields"
													:v-model="''"
												>
												</FormElementWithValidation>
											</b-td>
										</b-tr>
									</ValidationObserver>
								</b-table-simple>
							</div>
							</b-card>
						</div>
						<div class="col-lg-6">
							<b-card
								:header="localeEl.headings.breakup"
								header-text-variant="white"
								header-bg-variant="blue"
								class="form-wrapper"
							>
							<table class="table table-striped">
								<tbody>
								<tr>
									<td width="30%"><b>Taxable Amount</b></td>
									<td width="10%">0</td>
									<td width="20%"></td>
									<td width="30%"><b>Charges</b></td>
									<td width="10%">0</td>
