								<tr>
									<td><b>SGST</b></td>
									<td>0</td>
									<td></td>
									<td><b>Commission</b></td>
									<td>0</td>
								</tr>
								<tr>
									<td><b>CGST</b></td>
									<td>0</td>
									<td></td>
									<td><b>Calamity</b></td>
									<td>0</td>
								</tr>
								<tr>
									<td><b>IGST</b></td>
									<td>0</td>
									<td></td>
									<td><b>Charges Cess</b></td>
									<td>0</td>
								</tr>
								<tr>
									<td><b>TCS</b></td>
									<td>0</td>
									<td></td>
									<td><b>Calamity Cees</b></td>
									<td>0</td>
								</tr>
								</tbody>
							</table>
							<div class="bg-warning rounded-pill">
										<div
											class="text-center p-1"
											style="color: #fff;"
										>
											Total : 0
										</div>
									</div>
								<!-- <div class="d-flex flex-column justify-content-between">
									<div class="d-flex flex-row justify-content-around">
										<div class="d-flex flex-column justify-content-around">
											<div
												style="font-size: 12px"
												class="d-flex flex-row justify-content-between"
											>
												<p style="color: #070f63" class="font-weight-bold pb-1">
													Taxable Amount
												</p>
												<p class="ml-5">0</p>
											</div>
											<div
												style="font-size: 12px"
												class="d-flex flex-row justify-content-between"
											>
												<p style="color: #070f63" class="font-weight-bold">SGST</p>
												<p class="ml-5">0</p>
											</div>
											<div
												style="font-size: 12px"
												class="d-flex flex-row justify-content-between"
											>
												<p style="color: #070f63" class="font-weight-bold">CGST</p>
												<p class="ml-5">0</p>
											</div>
											<div
												style="font-size: 12px"
												class="d-flex flex-row justify-content-between"
											>
												<p style="color: #070f63" class="font-weight-bold">IGST</p>
												<p class="ml-5">0</p>
											</div>
											<div
												style="font-size: 12px"
												class="d-flex flex-row justify-content-between"
											>
												<p style="color: #070f63" class="font-weight-bold">TCS</p>
												<p class="ml-5">0</p>
											</div>
										</div>
										<div class="d-flex flex-row justify-content-around">
											<div class="d-flex flex-column justify-content-around">
												<div
													style="font-size: 12px"
													class="d-flex flex-row justify-content-between"
												>
													<p style="color: #070f63" class="font-weight-bold">Charges</p>
													<p class="ml-5">0</p>
												</div>
												<div
													style="font-size: 12px"
													class="d-flex flex-row justify-content-between"
												>
													<p style="color: #070f63" class="font-weight-bold">Commission</p>
													<p class="ml-5">0</p>
												</div>
												<div
													style="font-size: 12px"
													class="d-flex flex-row justify-content-between"
												>
													<p style="color: #070f63" class="font-weight-bold">Calamity</p>
													<p class="ml-5">0</p>
												</div>
												<div
													style="font-size: 12px"
													class="d-flex flex-row justify-content-between"
												>
													<p style="color: #070f63" class="font-weight-bold">Charges Cess</p>
													<p class="ml-5">0</p>
												</div>
												<div
													style="font-size: 12px"
													class="d-flex flex-row justify-content-between"
												>
													<p style="color: #070f63" class="font-weight-bold">
														Calamity Cees
													</p>
													<p class="ml-5">0</p>
												</div>
											</div>
										</div>
									</div>
									<div style="background: #070f63">
										<div
											class="text-center p-1"
											style="color: #fff;"
										>
											Total : 0
										</div>
									</div>
								</div> -->
							</b-card>
						</div>
					</div>
				</div>
			</ValidationObserver>
		</template>

		<template v-slot:footer>
			<button
				v-if="$config.fillDummyData"
				class="btn btn-success"
				@click="fillDummyData()"
			>
				Fill Data
			</button>
			<label for="submit_form" class="btn btn-primary">
				<span v-if="scope == 'create'">Save</span>
				<span v-else>Save and Update</span>
			</label>
		</template>
	</ModalView>
</template>

<script>
import { DataEntryMixin } from "@/mixins/data-entry-mixin";

export default {
	name: "CurrencyNotesForm",
	mixins: [DataEntryMixin],
	data() {
		return {
			collection: "currency",
			localeName: "Currency_Notes.Currency_Notes",
			vm: {},
			scope: "create",
			reference: null,
			filter: null,
			fields: [],
			items: [],
			currencyOptions:[{'code':'INR', 'label':'INR'}, {'code':'USD', 'label':'USD'}, {'code':'EUR', 'label':'EUR'}],
			currencyList:[{
				CURRENCY:"USD",
				FC_AMOUNT:"0",
				RATE:"0",
			},{
				CURRENCY:"EUR",
				FC_AMOUNT:"0",
				RATE:"0",
			},{
				CURRENCY:"THB",
				FC_AMOUNT:"0",
				RATE:"0",
			}]
		};
	},
	mounted() {
		this.fields.push(
			{ key: "CURRENCY", label: this.localeEl.fields.CURRENCY, sortable: true },
			{
				key: "FC_AMOUNT",
				label: this.localeEl.fields.FC_AMOUNT,
				sortable: true,
			},
			{
				key: "CUSTOMER_RATE",
				label: this.localeEl.fields.CUSTOMER_RATE,
				sortable: true,
			}
		);
	},
	created() {
		if (this.$route.query.reference) {
			this.reference = JSON.parse(this.$route.query.reference);
			this.$set(this.vm, "reference", this.reference);
		}
	},
	watch: {},
	computed: {
		el() {
			return getEl(this);
		},
		localeEl() {
			return this.$t(this.localeName);
		},
		modal_title() {
			const action = this.scope == "create" ? "Add" : "Modify";
			return `${this.localeEl.ModalTitle}`;
		},
	},
	methods: {
		fillDummyData() {
			this.vm = {
				...this.vm,
			};
		},
	},
};

function getEl(vm) {
	console.log(vm);
	return {
		TopGeneralDetailEl: [
			// {
			// 	type: "checkbox",
			// 	name: "Puschase_Sale",
			// 	ds: "ds-poa",
			// 	switch: true,
			// 	"checked-val": "Puschase",
			// 	"unchecked-val": "Sale",
			// 	value: "No",
			// 	class: "span50 radio-align",
			// },
			{
				type: "text",
				name: "CORPORATE",
				value: "MISC",
				rules: { required: false, max: [50] },
			},
			{
				type: "select",
				ds: "AP.SALUTATION",
				"ds-code": "CODE",
				"ds-name": "DESCR",
				name: "PURPOSE",
				rules: { max: [50] },
			},
		],
		GeneralDetailEl: [
			{
				type: "text",
				name: "NAME",
				rules: { required: false, max: [50] },
			},
			{
				type: "select",
				ds: "AP.SALUTATION",
				"ds-code": "CODE",
				"ds-name": "DESCR",
				name: "DOCUMENT_TYPE",
				rules: { max: [50] },
			},
			{
				type: "number",
				name: "DOCUMENT_ID",
				rules: { required: false, max: [50] },
			},
			{
				type: "number",
				name: "OLD_DOCUMENT_ID",
				rules: { required: false, max: [50] },
			},
			{
				type: "number",
				name: "TITLE",
				rules: { required: false, max: [50] },
			},
			{
				type: "text",
				name: "ISSUE_AT",
				rules: { required: false, max: [50] },
			},
			{
				type: "date",
				name: "ISSUE_DATE",
				rules: { required: false },
				futureDates: false,
			},
			{
				type: "date",
				name: "EXPIRY_DATE",
				rules: { required: false },
				futureDates: false,
			},
			{
				type: "date",
				name: "DOB",
				rules: { required: false },
				futureDates: false,
			},
			{
				type: "text",
				name: "OCCUPATION",
				rules: { required: false, max: [50] },
			},
			{
				type: "text",
				name: "ADDRESS_LINE_1",
				rules: { required: false, max: [50] },
			},
			{
				type: "text",
				name: "ADDRESS_LINE_2",
				rules: { required: false, max: [50] },
			},
			{
				type: "select",
				ds: "AP.SALUTATION",
				"ds-code": "CODE",
				"ds-name": "DESCR",
				name: "CITY",
				rules: { max: [50] },
			},
			{
				type: "text",
				name: "District",
				rules: { required: false, max: [50] },
			},
			{
				type: "number",
				name: "PINCODE",
				rules: { required: false, max: [50] },
			},
			{
				type: "text",
				name: "NATIONALITY",
				rules: { required: false, max: [50] },
			},
			{
				type: "select",
				ds: "AP.SALUTATION",
				"ds-code": "CODE",
				"ds-name": "DESCR",
				name: "RESIDENT_PERMIT",
				rules: { max: [50] },
			},
			{
				type: "number",
				name: "STATE_CODE",
				rules: { required: false, max: [50] },
				disabled: true,
			},
			{
				type: "checkbox",
				name: "IS_MINOR",
				rules: { required: true, max: [10] },
				class: "align-self-center pt-4",
			},
			{
				type: "hidden",
				class: "span100",
			},
			{
				type: "text",
				name: "PAN_GUARDIAN_PAN",
				rules: { required: false, max: [50] },
			},
		],
	};
}
</script>

