<script>
import MainScreenLayout from "@/mixins/Main-Screen-Layout";
import CardView from "./components/CardView";
import Filters from "./components/Filters";

export default {
	mixins: [MainScreenLayout],
	components: {
		CardView,
		Filters,
	},
	data() {
		return {
			collection: "ap",
			localeName: "Currency_Notes.Currency_Notes",
			referenceKey: ["AP_ID"],
			fields: [],
			isCheckbox: false,
			isAction: false,
			filterSetting: { card: false, grid: false, upload: false, download: false },
			defaultFilter: [],
			staticFilter: [],
			defaultFilter: [],
		};
	},
	computed: {
		columns() {
			return [
				{ key: "AP_ID" },
				{ key: "AP_NAME" },
				{ key: "SALUTATION" },
				{ key: "SALUTATION" },
				{
					key: "AUTHORIZATION_STATUS",
					formatter: (value, key, item) => {
						return `<span class="badge badge-pill badge-${this.$options.filters.status_label_variant(
							value
						)}">${this.$options.filters.status_label(
							value,
							this.$t("status_labels")
						)}</span>`;
					},
				},
				{ key: "CREATED_ON" },
				{ key: "CATEGORY_CODE" },
			];
		},
		filterEl() {
			return [];
		},
	},
	created() {},
};
</script>
