<template>
	<section>
		<!-- Pagination Bar Start -->
		<div class="card-header d-flex justify-content-between pb-0 mb-0 border-0">
			<span>{{ paginationLabel }}</span>
			
			<b-pagination
				ref="pagination"
				size="sm"
				pills
				align="right"
				v-model="currentPage"
				@change="onPageChanged"
				:per-page="perPage"
				:total-rows="recordDetails.totalRecord || localItems.length + 1"
				class="mb-0"
			></b-pagination>
			
		</div>
		<!-- Pagination Bar End -->
		<section class="card-table" v-show="isBusy || localItems.length">
			<div class="card">
				<div class="card-body p-0">
					<b-table
						ref="selectableTable"
						:busy.sync="isBusy"
						:sort-by.sync="sortBy"
						:sort-desc="false"
						sort-icon-left
						thead-class="thead-dark"
						striped
						hover
						sm
						borderless
						sticky-header
						head-variant="dark"
						responsive="sm"
						:items="myProvider"
						:fields="[
							isAction == 'false' && isCheckbox == 'false' ? '' : 'action',
							...fields,
						]"
						:per-page="perPage"
						:current-page="currentPage"
						class="mb-0"

						:no-provider-sorting="true"
						
					>
						<template v-slot:cell(action)="data">
								<span class="btn-group" v-if="isCheckbox == true">
									<input :value="data.item" v-model="checkedItem_vm" type="checkbox" />
								</span>
								<component
									:is="Actions"
									:item="data.item"
									:routePath="$route.path"
									:collection="collection"
									:referenceKey="referenceKey"
									:localeEl="localeEl"
									:showIcons="true"
									:showDots="true"
									v-if="isAction == true"
							>
							</component>
						</template>

						<template v-for="field in fields" v-slot:[`cell(${field.key})`]="data">
							<slot :name="field.key" :v-html="data.item"
								><span v-html="data.value" :key="fields.key"></span
							></slot>
						</template>

						<template #table-busy>
							<div class="text-center text-danger my-2">
								<b-spinner class="align-middle"></b-spinner>
								<strong class="ml-2">Loading...</strong>
							</div>
						</template>
					</b-table>
				</div>
			</div>
		</section>

		<no-data v-if="!isBusy && !localItems.length">
			<template v-slot:msg>
				<h5 class="text-muted">
					Sorry! no data found for {{ localeEl.PageTitle }}
				</h5>
			</template>
		</no-data>
		
	</section>
</template>
<script>
import { ActionMixin } from "@/mixins/action-mixin";
import ViewPagination from "../Pagination/ViewPagination.vue";

export default {
	components: { ViewPagination },

	mixins: [ActionMixin],

	props: {
		fetchData: {
			type: Function,
			required: true,
		},
		fields: Array,
		collection: String,
		referenceKey: Array,
		localeEl: Object,
		isCheckbox: Boolean,
		isAction: Boolean,
		perPage: {
			type: Number,
			default: 10,
		},
	},
	data() {
		return {
			currentPage: 1,
			isBusy: false,
			sortBy: (this.fields[1] || {}).key || "ID",
			localItems: [],
		};
	},
	computed: {
		recordDetails() {
			let lastRecord = (this.currentPage - 1) * this.perPage + this.perPage;
			let rc_interval = ((this.localItems || [])[0] || {}).RC_INTERNAL;
			lastRecord = lastRecord > rc_interval ? rc_interval : lastRecord;
			return {
				fisrtRecord: (this.currentPage - 1) * this.perPage + 1,
				lastRecord,
				totalRecord: rc_interval,
			};
		},
		paginationLabel() {
			let label = `Records ${this.recordDetails.fisrtRecord} - ${this.recordDetails.lastRecord}`;
			if ( this.recordDetails.totalRecord == null || this.recordDetails.totalRecord == "") 
				return "";	
			return label += ` of ${this.recordDetails.totalRecord}`;
		},
	},

	methods: {
		onPageChanged() {
			this.$emit("clear:selected:filter");
		},

		refereshData() {
			this.updateLocalData([]);
			this.currentPage = 1;
			this.$refs.selectableTable.refresh();
		},

		myProvider(ctx) {
			const _this = this;
			_this.isBusy = true;
			let start = (parseInt(ctx.currentPage) - 1) * parseInt(ctx.perPage) + 1 - 1;
			return this.fetchData({
				start,
				limit: ctx.perPage,
			})
				.then((response) => {
					_this.isBusy = false;
					return _this.updateLocalData(response);
				})
				.catch((error) => {
					_this.isBusy = false;
					console.error(error);
					return _this.updateLocalData([]);
				});
		},
		updateLocalData(records) {
			this.$set(this, "localItems", records || []);
			this.$emit("update:data:source", this.localItems);
			return this.localItems;
		},
		updateBusyState(bool) {
			this.isBusy = bool;
		},

		onRowSelected(items) {
			this.selected = items;
		},
		selectAllRows() {
			this.$refs.selectableTable.selectAllRows();
		},
		clearSelected() {
			this.$refs.selectableTable.clearSelected();
		},
		selectRow(index) {
			this.$refs.selectableTable.selectRow(index);
		},
		unselectThirdRow(index) {
			this.$refs.selectableTable.unselectRow(index);
		},
	},
};
</script>
