<template>
<section class="list-view">
    <!-- Pagination Bar Start -->
	<view-pagination 
		v-model="currentPage"
		:per-page="perPage"
		:call-rest-api="fetchData"
		@busy:state="updateBusyState"
        @local:data:changed="updateLocalData"
        ref="viewpagination"
        @clear:selected:filter="$emit('clear:selected:filter')"
	/>
	<!-- Pagination Bar End -->

    <no-data v-if="!isBusy && !localItems.length">
        <template v-slot:msg>
            <h5 class="text-muted">
                Sorry! no data found for {{ pageTitle }}
            </h5>
        </template>
	</no-data>
   
    <section class="card-grid" v-scroll>
        <div v-for="(item, index) in localItems"  :key="`card_${index}`">
            <slot v-bind="{ item, index }"></slot>
        </div>
    </section>    

</section>
</template>

<script>

import ViewPagination from '../Pagination/ViewPagination.vue';
import { scroll } from "@/directives/infiniteScrolling";

export default {
	name: "list-view",

    directives: {
		scroll: scroll,
	},

    components: { ViewPagination },
    
    props: {
        pageTitle: {
            type: String
        },
        perPage: {
            type: Number,
            default: 10,
	    },
        fetchData: {
            type: Function,
            required: true
        },
    },

    data() {
		return {
			currentPage: 1,
            localItems: Object.freeze([]),
            isBusy: false
		};
	},

    methods: {
        refereshData() {
            this.updateLocalData([]);
            this.currentPage = 1;
            this.$refs.viewpagination.refereshData();
        },
        updateLocalData(records){
            this.$set(this, 'localItems', records||[]);
            this.$emit("update:data:source", this.localItems);
            return this.localItems;
        },
        updateBusyState(bool){
            this.isBusy = bool;
        }
    },
};
</script>
